<template>
  <div id="app">
    <hello-world></hello-world>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

export default {
  name: "app",
  components: {
    HelloWorld
  }
};
</script>

<style>

#app {
  width: 100%;
  height: 100%;
  min-height: 1200px;
  background: linear-gradient(141deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
}

@media screen and (min-width: 976px) {
  #app {
    min-height: 700px;
  }
}
</style>



